        :root {
          --primary: #4f46e5;
          --primary-hover: #4338ca;
          --bg: #f8fafc;
          --white: #ffffff;
          --text-main: #0f172a;
          --text-muted: #64748b;
          --border: #e2e8f0;
          --error: #ef4444;
        }

        .app-container {
          min-height: 100vh;
          background-color: var(--bg);
          display: flex;
          flex-direction: column;
          font-family: system-ui, -apple-system, sans-serif;
          color: var(--text-main);
        }

        .navbar {
          background: var(--white);
          border-bottom: 1px solid var(--border);
          padding: 1rem 1.5rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          box-shadow: 0 1px 2px rgba(0,0,0,0.05);
        }

        .logo {
          display: flex;
          align-items: center;
          gap: 0.5rem;
          font-weight: bold;
          font-size: 1.25rem;
          color: var(--primary);
        }

        .logo-icon {
          background: var(--primary);
          padding: 0.4rem;
          border-radius: 8px;
          color: white;
          display: flex;
        }

        .logout-btn {
          display: flex;
          align-items: center;
          gap: 0.5rem;
          background: none;
          border: none;
          font-size: 0.875rem;
          font-weight: 500;
          color: var(--text-muted);
          cursor: pointer;
          transition: color 0.2s;
        }

        .logout-btn:hover {
          color: var(--error);
        }

        .main-content {
          flex: 1;
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 1.5rem;
        }

        .card {
          background: var(--white);
          width: 100%;
          border-radius: 1rem;
          box-shadow: 0 10px 25px -5px rgba(0,0,0,0.1);
          padding: 2rem;
        }

        .card-auth { max-width: 400px; }
        .card-dash { max-width: 650px; }

        .form-group { margin-bottom: 1rem; }
        
        .form-label {
          display: block;
          font-size: 0.875rem;
          font-weight: 600;
          color: #475569;
          margin-bottom: 0.25rem;
          text-transform: capitalize;
        }

        .input-wrapper { position: relative; }

        .input-icon {
          position: absolute;
          left: 0.75rem;
          top: 50%;
          transform: translateY(-50%);
          color: #94a3b8;
          display: flex;
        }

        .form-input {
          width: 100%;
          padding: 0.6rem 0.75rem 0.6rem 2.5rem;
          border: 1px solid var(--border);
          border-radius: 0.5rem;
          outline: none;
          transition: all 0.2s;
          box-sizing: border-box;
        }

        .form-input:focus {
          border-color: var(--primary);
          box-shadow: 0 0 0 2px rgba(79, 70, 229, 0.1);
        }

        .input-error { border-color: var(--error); }

        .error-text {
          color: var(--error);
          font-size: 0.75rem;
          margin-top: 0.25rem;
          display: flex;
          align-items: center;
          gap: 0.25rem;
        }

        .primary-btn {
          width: 100%;
          background: var(--primary);
          color: white;
          padding: 0.75rem;
          border: none;
          border-radius: 0.5rem;
          font-weight: bold;
          cursor: pointer;
          transition: background 0.2s;
          margin-top: 1rem;
        }

        .primary-btn:hover { background: var(--primary-hover); }

        .secondary-btn {
          background: var(--white);
          border: 1px solid var(--border);
          padding: 0.75rem;
          border-radius: 0.75rem;
          font-weight: 600;
          cursor: pointer;
          transition: background 0.2s;
        }

        .secondary-btn:hover { background: var(--bg); }

        .auth-footer {
          margin-top: 1.5rem;
          text-align: center;
          font-size: 0.875rem;
          color: var(--text-muted);
        }

        .text-link {
          color: var(--primary);
          background: none;
          border: none;
          font-weight: bold;
          cursor: pointer;
        }

        .user-profile {
          display: flex;
          align-items: center;
          gap: 1rem;
          margin-bottom: 2rem;
        }

        .avatar {
          width: 4rem;
          height: 4rem;
          background: #e0e7ff;
          color: var(--primary);
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 1.5rem;
          font-weight: bold;
        }

        .quote-box {
          background: var(--bg);
          border: 1px solid var(--border);
          border-radius: 0.75rem;
          padding: 1.5rem;
          position: relative;
          text-align: center;
        }

        .quote-icon {
          position: absolute;
          top: 1rem;
          left: 1rem;
          color: #c7d2fe;
          opacity: 0.5;
        }

        .quote-text {
          font-style: italic;
          font-size: 1.125rem;
          margin-bottom: 1rem;
          padding: 0 1rem;
        }

        .quote-author {
          font-weight: 600;
          color: var(--primary);
        }

        .grid-2 {
          display: grid;
          grid-template-columns: 1fr 1fr;
          gap: 1rem;
          margin-top: 2rem;
        }

        .loader {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 0.5rem;
          color: var(--text-muted);
        }

        .animate-spin {
          animation: spin 1s linear infinite;
        }

        @keyframes spin {
          from { transform: rotate(0deg); }
          to { transform: rotate(360deg); }
        }
     
      